import { Component, OnInit } from '@angular/core';
import * as firebase from 'firebase';
import { Router } from '@angular/router';
import { ToastController } from '@ionic/angular';
import { NgForm, NgModel } from '@angular/forms';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.page.html',
  styleUrls: ['./signup.page.scss'],
})
export class SignupPage implements OnInit {

  user={
    email:"",
    password:"",
    type:false};
  
  acc:string = "Visitor";

    imgfile = "/assets/white.png";
  
    constructor(private router:Router,
      private toastController:ToastController) { }
  
    ngOnInit() {
    }
  
    async presentToast() {
      const toast = await this.toastController.create({
        color:'success',
        message: 'Account Created! Login to get started',
        position:'top',
        duration: 2000
      });
      toast.present();
    }

    signup(){
      console.log(this.user.email+"  "+this.user.password)
      var email=this.user.email;
      var password=this.user.password;
      var type = this.user.type;
      if(type == true) {
        var usertype = "owner";
      }
      else {
        var usertype = "visitor";
      }
      var self = this;


      firebase.auth().createUserWithEmailAndPassword(email, password).catch(
        function(error) {
      // Handle Errors here.
      console.log(error);
      var errorCode = error.code;
      var errorMessage = error.message;
      console.log(error.message);
      if(errorCode.length > 0){
        console.log("Failed");
      }
      else{
        console.log("signup ok")
      }
    }).then(function(result){
      var user= firebase.auth().currentUser;
			 var db = firebase.firestore();
		          db.collection("usertype").add({
		            'uid':user.uid,
		            'usertype': usertype
		      })
		      .then(function(docRef) {
		          console.log("usetype written with ID: ", docRef.id);
		      })
		      .catch(function(error) {
		          console.error("Error adding document: ", error);
		      });

		  	console.log("finished creating account")
        console.log(user.uid);
        self.presentToast();
        self.router.navigate(["/login"]);
    });

    NgModel.arguments.reset();
    }

    
    accType() {
      if(this.user.type == false)
      this.acc = "Owner";
      else
      this.acc = "Visitor";
    }
  }

